.header {
    width: 100%;
    height: var(--header-height);
    background-color: var(--c-header);
    position: fixed;
    z-index: 100;
    top: 0;
    transition: background var(--trans-dur), top var(--trans-dur);
    box-shadow: 0 5px 5px -5px rgba(0, 0, 0, .5);
}

.nav__dropdown {
    right: 0;
    top: calc(var(--header-height) - 15px / 2);
}

.header.drop {
    background-color: var(--c-header-drop-bg);
    backdrop-filter: blur(10px);
}

.nav {
    height: calc(var(--header-height));
}

.nav__list {
    height: calc(var(--header-height));
    align-items: center;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}

.nav__item-logo {
    justify-self: start;
}

.nav__item-menu {
    justify-self: end;
}

.nav__item {
    display: flex;
    flex-direction: row;
    justify-content: center;
    height:  calc(var(--header-height) - 15px);
}

.nav__action {
    height: calc(var(--header-height) - 15px);
    padding: 0 15px;
    opacity: 0.8;
    border-radius: 10px;
}

.nav__action.logo {
    padding: 0 15px 0 0;
}

.nav__action:hover {
    opacity: 1;
    background-color: var(--c-hover);
}

.nav__action.logo:hover {
    background-color: inherit;
    opacity: 1;
}

.nav__theme-switcher {
    margin: 0 20px 0 0;
    align-self: center;
}

.fake-header {
    height: var(--header-height);
    margin: 0 0 15px 0;
}

.header.header_hidden {
    top: -60px;
}

.Menu {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.Menu__body {
    height: 100%;
    display: flex;
}

.Menu__link {
    font-size: 13px;
    line-height: 15px;
    display: inline-block;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.Menu__link {
    position: relative;
    width: 85px;
}

.Menu__text {
    position: absolute;
    top: calc(50% - 15px/2);
    left: 0;
    opacity: 0;
    width: 100%;
    text-align: center;
}

.Menu__link:hover > i {
    opacity: 0;
}

.Menu__link:hover > .Menu__text {
    opacity: 1;
}

.Menu__text, .Menu__link > i {
    transition: opacity 0.3s;
}

.meesage-i {
    --ggs: 1.15;
}

.image-i {
    --ggs: 0.9;
}

.authUserMenu {
    display: flex;
    align-items: center;
    gap: 20px;
}